self.addEventListener('message', function (e) {
    const {title, coins, sortDirection} = e.data;

    const sortedCoins = coins.sort((coinA, coinB) => {
        const coinAValue = coinA[title];
        const coinBValue = coinB[title];

        const A = (title === 'name' || title === 'symbol') ? coinAValue.toUpperCase() : parseFloat(coinAValue);
        const B = (title === 'name' || title === 'symbol') ? coinBValue.toUpperCase() : parseFloat(coinBValue);

        const a = isNaN(A) ? 0 : A;
        const b = isNaN(B) ? 0 : B;

        if (sortDirection === 'desc') {
            if (a < b) {
                return -1;

            } else if (a > b) {
                return 1;
            }

            return 0;

        } else {
            if (a > b) {
                return -1;

            } else if (a < b) {
                return 1;
            }

            return 0;
        }
    });

    self.postMessage({
        title: title,
        coins: sortedCoins,
        direction: sortDirection === 'desc' ? 'asc' : 'desc'
    });

}, false);


